import React, { Component } from 'react';
import AuthenService from '../authentication/AuthenService.js';
import {withRouter} from "react-router-dom";
import Axios from 'axios';

class  Login extends Component {      
       
  constructor(props) {
    super(props)
    this.state={
      username:'user',
      password:'user', 
      showInvalid:false
    } 

    this.userInputChange = this.userInputChange.bind(this)
    this.submitLogin = this.submitLogin.bind(this)
}
 


submitLogin(event){
  event.preventDefault();
   
      if(this.state.username==='user'&&this.state.password==='user'){
        
        AuthenService.registerSuccessfulLogin(this.state.username,this.state.password);
        this.props.history.push('/admin/Profile');
      }else{ this.setState({
        showInvalid:true 
               });}
 
}

  // submitLogin(event){
  //   event.preventDefault();
  //   console.log('exedf');
  //       Axios.post('http://localhost:8080/authenticate', {
  
  //          "username":this.state.username,        
  //         "password":this.state.password
  //       }).then(response=>{
  //     AuthenService.registerSuccessfulLogin(this.state.username,this.state.password,response.data.token);
  //           this.props.history.push('/work/');
          
  //       }
  //       ).catch(error=>{console.log("authenticate response error");
  //                           this.setState({
  //                           showInvalid:true 
  //                                  });
  //     });
  // }


  userInputChange(event){
    
    this.setState(
        {
            [event.target.name]
              :event.target.value
        }
    );
}
  render() {
    return (
     
      <div className="login_bg">
      <div className="login_panel">
            <form action="#" >
              <h1>LOGIN</h1>
                  <input type="text" name="username"  value={this.state.username}  onChange={this.userInputChange}/>
                  <input type="password" name="password"   value={this.state.password}  onChange={this.userInputChange}/>
                  <button type="submit"  onClick={this.submitLogin}>LOGIN</button>
                  <div className="home_login-message">
                      <span>Not registered?</span>
                      <a href="">Create an account</a>
                  </div>
                  
                  { this.state.showInvalid &&
                    <span className ="text-warning">
                      wrong username or password.
                    </span>
                  }
            </form>
      </div>
    </div>
  
    )
  }
}


export default withRouter(Login);


