import React, { Component } from 'react'
import axios from 'axios';
import AuthenService  from '../authentication/AuthenService.js';

export default class Dashboard extends Component {
  constructor(props){
    super(props);
    this.state={
    
    };
    this.receiveList=this.receiveList.bind(this);
  }

 
  receiveList(){
    axios.get('http://localhost:8080/users/user/todos',{
      headers: {
        Authorization: `Bearer ${sessionStorage.getItem('token')}`,
      }
    }).then(
      response=>{this.setState(response.data);})
       .catch(error=>{console.log('list response error happened')});
  
  }
 
  render() {
    return (
      <div>
      <h1> this is dash board</h1> 
       
      <button class="btn btn-lg btn-primary" onClick={this.receiveList}>get data </button> 
      </div>
    )
  }
}



