import React, { Component } from 'react'
import CatPic from '../../resources/images/cat.jpg';
export default class Profile extends Component {
  render() {
    return (
      
        <div className="profile_container">
          <form className="profile_form" action="#" method="post">

            <h3>Edit Profile</h3>
            <div>
              <div className="profile_finput">
                <label  >COMPANY</label>
                <input type="text" />
              </div>
              <div className="profile_finput">
                <label >EMAIL ADDRESS</label>
                <input type="email" />
              </div>
            </div>


            <div>
              <div className="profile_finput">
                <label >FIRST NAME</label>
                <input type="text" />
              </div>

              <div className="profile_finput">
                <label >LAST NAME</label>
                <input type="text" />
              </div>
            </div>


            <div>
              <div className="profile_finput">
                <label >ADDRESS</label>
                <input type="text" />
              </div>
            </div>

            <div>
              <div className="profile_finput">
                <label >CITY</label>
                <input type="text" />
              </div>
              <div className="profile_finput">
                <label >COUNTRY</label>
                <input type="text" />
              </div>
              <div className="profile_finput">
                <label  >POSTAL CODE</label>
                <input type="text" />
              </div>
            </div>


            <div>
              <div className="profile_finput">
                <label >ABOUT ME</label>
                <input type="text" />
              </div>
            </div>

            <button type="submit"> update profile</button>
          </form>




          <div className="profile_img">
            <img src={CatPic} alt="cat at current" />
            <h5>Mike Andrew</h5>
            <p>"Lamborghini MercyYour chick she so thirstyI'm in that two seat Lambo"</p>

          </div>

        </div>
    

    );
  }
}



