import React, { Component } from 'react';
import {
  HashRouter as Router,
  Switch,
  Route 
  
} from "react-router-dom";
import Login from '../login/Login';
import MyWork from '../work/MyWork';
import AuthenRoute from './AuthenRoute';
import Dashboard from '../Main_area/Dashboard';
import Profile from '../Main_area/Profile';
import Tabledata from '../Main_area/Tabledata'
export default class AppRouter extends Component {
  render() {
    return (
      <Router>
      <div>
        <Switch>
        <Route path="/" exact>
            <Login/>
          </Route>
          <Route path="/login">
            <Login/>
          </Route>
         
          
    
          <AuthenRoute path="/admin"  render={()=>
            <MyWork>
         
            <Route path="/admin/Dashboard" component={Dashboard} />  
            <Route path="/admin/Profile" component={Profile} />   
            <Route path="/admin/Tabledata" component={Tabledata} />  
           
            </MyWork>
          
            }   />
          
          
          

            
        </Switch>

        </div>
      </Router>
    )
  }
}



